// middleware.ts
import { clerkMiddleware, createRouteMatcher } from "@clerk/nextjs/server";

// Mark routes that should NOT require auth:
const isPublicRoute = createRouteMatcher([
    "/",
    "/sign-in(.*)",
    "/sign-up(.*)",
    "/api/public(.*)",
]);

export default clerkMiddleware((auth, req) => {
    if (!isPublicRoute(req)) {
        auth.protect();
    }
});

// Make sure middleware runs on app + api routes (and ignores static files)
export const config = {
    matcher: [
        "/((?!.*\\..*|_next).*)",
        "/",
        "/(api|trpc)(.*)",
    ],
};
